<template>
  <!-- 主体区域 -->
  <section id="app">
  <TodoHead @add="handleAdd"></TodoHead>
  <TodoMain @delete="handleDel" :list="list"></TodoMain>
  <TodoFoot :list="list" @clear="handleClear"></TodoFoot>
  
  </section>
</template>

<script>
import TodoHead from './components/TodoHead.vue';
import TodoMain from './components/TodoMain.vue';
import TodoFoot from './components/TodoFoot.vue';
export default {
  data () {
    return {
      list:JSON.parse(localStorage.getItem('list')) ||[{
        
      }]
    }
 
  },
  components:{
    TodoHead,
    TodoFoot,
    TodoMain
  },
  methods :{
    handleAdd(todoName)
    {

      this.list.unshift(
        {
          id : + new Date(),
          name :todoName
        }
     )
    },
    handleDel(TaskId){
      this.list = this.list.filter(item => item.id != TaskId)
    },
    handleClear(){
      this.list=[]
    }
  },
  watch:{
    list:{
      deep :true,
      handler (newValue){
        localStorage.setItem('list',JSON.stringify(newValue))
      }
    }
  }
}
</script>

