<template>
    <!-- 统计和清空 -->
    <footer class="footer">
      <!-- 统计 -->
      <span class="todo-count">合 计:<strong> {{list.length }}</strong></span>
      <!-- 清空 -->
      <button  @click="ClearTask"  class="clear-completed">
        清空任务
      </button>
    </footer>
</template>

<script>
export default {
  props:{
    list:Array
  },
  methods:{
    ClearTask(){
      this.$emit('clear')
    }
  }
}
</script>

